
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
data() {
return {
posterList: [],
personalized: [],
privatecontent: [],
newsong: []
};
},
components: {
Swiper,
Flexbox,
FlexboxItem,
SwiperItem,
Toast
},
computed: {},
methods: {
getData() {
var _this = this
this.$http
.all([
this.$musicApi.getBanner(this),
this.$musicApi.getPersonalized(this, { limit: 6 })
])
.then(
this.$http.spread(function(res1, res2) {

this.posterList = res1.data.banners;
this.personalized = res2.data.result.slice(0,6);
}.bind(this))
)
.catch(err => {
this.$msgbox('温馨提示','无法请求数据，请查看网络')
});
this.$http
.all([
this.$musicApi.getPrivatecontent(this),
this.$musicApi.getNewsong(this, { limit: 10 })
])
.then(
this.$http.spread(function(res1, res2) {

this.privatecontent = res1.data.result;
this.newsong = res2.data.result;
}.bind(this))
)
.catch(err => {
this.$msgbox('温馨提示','无法请求数据，请查看网络')
});
}
},
mounted: function() {
this.$nextTick(function() {
this.getData();
});
}
};



